.notfound {
    text-align: center;
    padding: 5rem 1rem;

    h1 {
        font-size: 3rem;
        margin-bottom: 1rem;
    }

    p {
        margin-bottom: 2rem;
        font-size: 1.25rem;
    }

    a {
        color: #00f0ff;
        font-weight: bold;
        text-decoration: none;

        &:hover {
            text-decoration: underline;
        }
    }
}